p {
  font-weight: 300;
}
div[data-for] {
  article > footer,
  article > section p:last-child {
    margin-bottom: 0;
  }
}
div[data-for="page-footer"] {
  font-size: rem-calc(14);
}
#copyright-text p {
  font-size: 13px
}

div[data-for="page-footer"] {
  a {
    line-height: 2;
    font-weight: 300;
    color: $body-font-color;
  }
}

h2 {
  color: map-get($onedesign-palette, brand-primary);
  font-weight: lighter;
  margin-bottom: 1rem;
}
div[data-for="agenda"] {
  h2,
  li {
    color: $white;
  }
}

strong {
  font-weight: 400;
}
div[data-for="info"] strong {
  color: map-get($onedesign-palette, brand-primary);
  display: block;
}

ol,
ul {
  list-style-type: none;
  padding-left: 0;
  margin-left: 0;
}
li p {
  margin-bottom: 0;
}

address {
  font-style: normal;
  font-weight: 300;
  span {
    font-weight: 400;
  }
}