$tip-label-input-icon-fsize: 1.25rem !default;
$tip-label-input-icon-lheight: 1.2 !default;

@font-face {
  font-family: "onedesign-icons";
  src: url("#{$site-font-url}/ui-icons.eot");
  src: url("#{$site-font-url}/ui-icons.eot?#iefix") format("embedded-opentype"),
       url("#{$site-font-url}/ui-icons.woff") format("woff"),
       url("#{$site-font-url}/ui-icons.ttf") format("truetype"),
       url("#{$site-font-url}/ui-icons.svg#fontcustom") format("svg");
  font-weight: normal;
  font-style: normal;
}

@mixin icon-sizes {
  &.small:before {
    font-size: 1rem;
  }
  &.medium:before {
    font-size: 2rem;
  }
  &.large:before {
    font-size: 3rem;
  }
};

@mixin onedesign-iconography {
  $selector: ();

  @each $name, $content in $onedesign-icons {
    $selector: append($selector, unquote('.oi-#{$name}:before, .oi-#{$name}.filled:before, .oi-#{$name}.outline:before'), 'comma');
  }

  #{$selector} {
    font-family: "onedesign-icons";
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    text-decoration: inherit;
  }

  // exception size for input tips
  .has-tip.label-input-tip,
  .has-tip.input-tip {
    > i[class*="oi-"]:before {
      font-size: $tip-label-input-icon-fsize;
      line-height: $tip-label-input-icon-lheight;
    }
  }

  @each $name, $content in $onedesign-icons {
    // filled icons
    .oi-#{$name}, .oi-#{$name}.filled {
      &:before {
        content: "\e#{$content}";
      }

      @include icon-sizes;
    }

    // outlined icons
    .oi-#{$name}.outline {
      &:before {
        content: "\f#{$content}";
      }

      @include icon-sizes;
    }

    @each $palette-name, $palette-color in $onedesign-palette {
      .oi-#{$name}.#{$palette-name}:before {
        color: #{$palette-color}
      }
    }
  }
}
