/// Default background color for buttons.
/// @type Color
$button-background: map-get($onedesign-palette, action) !default;

/// Background color on hover for buttons.
/// @type Color
$button-background-hover: scale-color($button-background, $lightness: -15%) !default;

/// Font color for buttons.
/// @type List
$button-color: $white !default;

/// Font color for buttons, if the background is light.
/// @type List
$button-color-alt: $black !default;


// TODO: Document button-base() mixin
@mixin onedesign-button-base {
  font-size: $onedesign-button-font-size;
  font-weight: $onedesign-button-font-weight;
  line-height: $onedesign-button-line-height;
}

// need this mixin since foundation doesn't expose the border width for hollow buttons
@mixin onedesign-button-hollow-style($color: $button-background, $border: $onedesign-button-border, $name: auto) {
  border: $border $color;

  // action and destructive don't have hollow style - following OneDesign specs
  @if $name == action or $name == destructive {
    color: foreground($color, $button-color-alt, $button-color);
    background-color: $color;

    // hover effects for these type of buttons should look like normal buttons, so
    // the following rules revert the defaults
    $background-hover: scale-color($color, $lightness: -20%);
    &:hover, &:focus {
      color: foreground($background-hover, $button-color-alt, $button-color);
      background-color: $background-hover;
      border-color: $background-hover;
    }
  }

  // since we have forced hollow buttons to have the default (primary-color) now
  // for other buttons we will revert them to transparent
  @else {
    background-color: transparent;

    &:hover, &:focus,
    &.#{$name}.disabled,
    &.#{$name}[disabled] {
      background-color: transparent;
    }
  }
}

/// adds the same background colours to buttons that are disabled
/// fixing an issue with zurb foundation
@mixin onedesign-button-disabled($color: $button-background) {
  $background-hover: scale-color($color, $lightness: -20%);
  &:hover, &:focus {
    background-color: $background-hover;
  }
}

@mixin onedesign-button {
  .button {
    @include onedesign-button-base;

    // Colors
    @each $name, $color in $foundation-palette {
      @if $button-fill == hollow {
        &.#{$name} {
          @include onedesign-button-hollow-style($color, $onedesign-button-border, $name);
        }
      }
    }

    // Hollow style
    @if $button-fill != hollow {
      &.hollow {
        @each $name, $color in $foundation-palette {
          &.#{$name} {
            @include onedesign-button-hollow-style($color, $onedesign-button-border, $name);
          }
        }
      }
    }

    // Disabled style
    @each $name, $color in $foundation-palette {
      &.disabled,
      &[disabled] {
        @include onedesign-button-disabled($button-background);
      }

      &.#{$name}.disabled,
      &.#{$name}[disabled] {
        @include onedesign-button-disabled($color);
      }
    }

    // Ghost Buttons
    @if $button-fill != hollow {
      @each $name, $color in $onedesign-palette {
        // action and destructive cannot be ghost buttons as per OneDesign specs
        @if $name != action and $name != destructive {
          &.#{$name}.ghost {
            background-color: transparent;
            border: 2px solid $button-color;

            $color-hover: scale-color($button-color, $lightness: -20%);
            &:hover, &:focus {
              background-color: transparent;
              color: $color-hover;
              border-color: $color-hover;
            }
          }
        }
      }
    }
  }

  // force the default hollow button to look like a normal button
  // as per OneDesing spec (using tag selector to force specificity)
  button.button.hollow,
  a.button.hollow {
    color: foreground($button-background, $button-color-alt, $button-color);
    background-color: $button-background;
    border-color: $button-background;

    $background-hover: scale-color($button-background, $lightness: -20%);
    &:hover, &:focus {
      color: $button-color;
      background-color: $background-hover;
      border-color: $background-hover;
    }
  }
}
