div[data-for="location"] {
  > article {
    display: flex;
    flex-direction: column;

    .map {
      order: -1;
      margin-bottom: 1rem;
      margin-left: calc(50% - 50vw); // forces it to start from the left edge of the page
      max-width: none;
      width: 100vw;
    }

    iframe {
      width: 100%;
    }
  }
}
