// Internal variables used for colors


/// OneDesign Palette - it will be defined in the settings, if so these values
///                     might not be the same as presented on this default
/// @type Map
// onedesign specific
$onedesign-palette: (
  action: #128020,
  progressive: #0D74AF,
  regressive: #687680,
  destructive: #D03027,
  brand-primary: #004977,
  brand-secondary: #D03027
) !default;

$action-color: map-get($onedesign-palette, action);
$progressive-color: map-get($onedesign-palette, progressive);
$regressive-color: map-get($onedesign-palette, regressive);
$destructive-color: map-get($onedesign-palette, destructive);
$brand-primary-color: map-get($onedesign-palette, brand-primary);
$brand-secondary-color: map-get($onedesign-palette, brand-secondary);

.content-middle {
  display: flex;
  align-items: center;
}
