@mixin onedesign-form-checkbox {
  input[type='checkbox'],
  input[type='radio'] {
    display: inline-block;
    width: 2em;
    height: 2.625em;
    margin: 0;
    padding: 0;
    vertical-align: middle;
    opacity: 0;

    border: $input-border;
    border-radius: $input-radius;
    transition: border-color .150s ease-in-out;
    outline: 0;

    + label {
      display: inline-block;
      margin-#{$global-left}: -2.3em;
      margin-bottom: .375em;

      &:before {
        content: "";
        position: relative;
        display: inline-block;
        height: 1.5em;
        width: 1.5em;
        margin: 0;
        margin-right: 4px;
        padding: 0;
        top: -3px;
        border: $input-border;
        background-color: $white;
        cursor: pointer;
        vertical-align: bottom;
        transition: all .150s ease-in-out;
      }
    }
  }

  input[type='checkbox'] {
    + label:before {
      border-radius: $input-radius;
    }

    &:focus + label:before {
      border-color: map-get($onedesign-palette, progressive);

      &:checked {
        background-color: map-get($onedesign-palette, progressive);
      }
    }

    &:checked {
      + label {
        position: relative;

        &:before {
          border-color: map-get($onedesign-palette, action);
          background-color: map-get($onedesign-palette, action);
        }
        &:after {
          content: "";
          position: absolute;
          display: inline-block;
          height: 1em;
          width: 1em;
          top: 0.250em;
          #{$global-left}: 0.250em;
          background-image: url("data:image/png;base64,");
          background-size: contain;
          color: $white;
        }
      }
    }
  }

  input[type='radio'] {
    + label:before {
      border-radius: 100%;
    }

    &:focus + label:before {
      border-color: map-get($onedesign-palette, progressive);
    }
    &.fill:focus + label:before {
      background-color: map-get($onedesign-palette, progressive);
    }

    &:checked + label:before {
      border-color: map-get($onedesign-palette, action);
      border-width: 6px;
    }

    &.fill:checked + label:before {
      background-color: map-get($onedesign-palette, action);
    }
  }
};
