// IMPORTANT:
//     These are all the possible variations of Optmist, on your project you
//     should only list the styles you need, usually one or two.
$optimist-styles: (
  'Extra-Light',
  'Extra-Light-Italic',
  'Light',
  'Light-Italic',
  'Regular',
  'Italic',
  'Semibold',
  'Semibold-Italic',
  'Bold',
  'Bold-Italic',
) !default;

// Base url for fonts on your site
$site-font-url: '/fonts' !default;

// Most of the times you won't need to override this variable. Only when you need
// to change the name of the font files on your project, or the default weights of
// style
$optimist-data: (
  'Extra-Light': ( file: 'Optimist_W_XLt', weight: 100, style: normal ),
  'Extra-Light-Italic': ( file: 'Optimist_W_XLtIt', weight: 100, style: italic ),
  'Light': ( file: 'Optimist_W_Lt', weight: 300, style: normal ),
  'Light-Italic': ( file: 'Optimist_W_LtIt', weight: 300, style: italic ),
  'Regular': ( file: 'Optimist_W_Rg', weight: 500, style: normal ),
  'Italic': ( file: 'Optimist_W_It', weight: 500, style: italic ),
  'Semibold': ( file: 'Optimist_W_SBd', weight: 700, style: normal ),
  'Semibold-Italic': ( file: 'Optimist_W_SBdIt', weight: 700, style: italic ),
  'Bold': ( file: 'Optimist_W_Bd', weight: 900, style: normal ),
  'Bold-Italic': ( file: 'Optimist_W_BdIt', weight: 900, style: italic ),
) !default;

@mixin onedesign-optimist($font-styles: $optimist-styles) {
  @each $font-style in $font-styles {
    $optimist-style: map-get($optimist-data, $font-style);

    @font-face {
      font-family: 'Optimist';
      font-style: map-get($optimist-style, style);
      font-weight: map-get($optimist-style, weight);
      src: url("#{$site-font-url}/#{map-get($optimist-style, file)}.eot"); /* IE9 Compat Modes */
      src: url('#{$site-font-url}/#{map-get($optimist-style, file)}.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
           url('#{$site-font-url}/#{map-get($optimist-style, file)}.woff2') format('woff2'), /* Super Modern Browsers */
           url('#{$site-font-url}/#{map-get($optimist-style, file)}.woff') format('woff'), /* Pretty Modern Browsers */
           url('#{$site-font-url}/#{map-get($optimist-style, file)}.ttf')  format('truetype'); /* Safari, Android, iOS */
    }
  }
}
