////
/// @group abide
////

/// Sets if misguided styles should be added to inputs.
/// @type Boolean
$abide-inputs: true !default;

/// Sets if misguided styles should be added to labels.
/// @type Boolean
$abide-labels: true !default;

/// Sets if misguided styles should be added to help text.
/// @type Boolean
$abide-help-text: true !default;

/// Background color to use for misguided inputs values.
/// @type Color
$input-background-misguided: map-get($foundation-palette, warning) !default;

/// Color to use for labels of misguided input values.
/// @type Color
$form-label-color-misguided: map-get($foundation-palette, warning) !default;

/// Color to use for help text of misguided input values
/// @type Color
$form-help-color-misguided: map-get($foundation-palette, warning) !default;

/// Default font color for form misguided input text.
/// @type Color
$input-misguided-color: map-get($foundation-palette, warning) !default;

/// Default font size for form misguided text.
/// @type Number
$input-misguided-font-size: rem-calc(12) !default;

/// Default font weight for form misguided text.
/// @type Keyword
$input-misguided-font-weight: $global-weight-bold !default;

/// Styles the background and border of an input field to have a misguided state.
///
/// @param {Color} $background [$warning-color] - Color to use for the background and border.
/// @param {Number} $background-alpha [0.1] - Transparency level of the background color.
@mixin onedesign-form-input-warning(
  $background: $input-background-misguided,
  $background-alpha: 0.1
) {
  &:not(:focus) {
    background-color: rgba($background, $background-alpha);
    border-color: $background;
  }
}

/// Adds warning styles to a form element, using the values in the settings file.
@mixin onedesign-warning {
  display: none;
  margin-top: $form-spacing * -0.5;
  margin-bottom: $form-spacing;
  font-size: $input-misguided-font-size;
  font-weight: $input-misguided-font-weight;
  color: $input-misguided-color;
}

@mixin onedesign-form-warning {
  @if $abide-inputs {
    // class for misguided input values
    .is-misguided-input {
      @include onedesign-form-input-warning;
    }
  }

  @if $abide-labels {
    // class for labels of misguided input values
    .is-misguided-label {
      color: $form-label-color-misguided;
    }
  }

  @if $abide-help-text {
    // class for help text of misguided input values
    .is-misguided-help {
      color: $form-help-color-misguided;
    }
  }

  // Form misguided element
  .form-misguided {
    @include onedesign-warning;

    &.is-visible {
      display: block;
    }
  }
}
