/*
 * Layout should only be used to divide a page into major regions and define the relationship
 * in terms of size, position, and layer between the parts.
 *
 * It should NOT be utilized to divide components, style and appearance of text elements,
 * forms or tables, etc.
 *
 * If your project have more than one page (page template) you should create one file for
 * each page/template your project requires and import them here in this file.
 */

 $small-header-height: 58px;
 $large-header-height: 68px;

 $small-footer-height: 350px;
 $medium-footer-height: 260px;
 $large-footer-height: 260px;

 $small-gutter: map-safe-get($grid-margin-gutters, small);
 $medium-gutter: map-safe-get($grid-margin-gutters, medium);

#page-header,
main > .content-wrapper > article,
main > .content-wrapper > section,
#page-footer {
  @include xy-grid-container;
}

body {
  main > .content-wrapper {
    @include xy-grid();

    > header,
    > section,
    > article,
    > footer {
      @include xy-cell($size: 12, $gutter-type: padding);
      @include xy-gutters($gutters: $triple-gutters, $gutter-type: padding, $gutter-position: left right top bottom);
    }
  }
}