div[data-for="speakers"] {
  article#speakers {
    padding-top: 0;
  }

  header {
    @include xy-gutters($gutters: $double-gutters, $gutter-type: margin, $gutter-position: bottom);
  }

  // image wrapper
  article > section > div:first-child {
    margin-left: calc(50% - 50vw); // forces it to start from the left edge of the page
    max-width: none;
    width: 100vw;
  }

  .name {
    margin-bottom: 0;
    margin-top: 1rem;
  }

  @include breakpoint(medium) {
    article > section {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;

      div:first-child {
        @include xy-cell($size: 6, $gutter-type: padding, $gutter-output: false);
        width: 50vw;
      }

      div:last-child {
        @include xy-cell($size: 6, $gutter-type: padding);

        // the following properties are required if background is not body background
        width: 50vw;
        margin-right: calc(50% - 50vw);
        background-color: $light-gray;
      }
    }
  }
}
