////
/// @group abide
////

/// Sets if error styles should be added to help text.
/// @type Boolean
$abide-help-text: true !default;

/// Color to use for help text of invalid inputs.
/// @type Color
$form-help-color-invalid: map-get($foundation-palette, alert) !default;


@mixin onedesign-form-error {
  @if $abide-help-text {
    // Error class for labels of invalid outputs
    .is-invalid-help {
      color: $form-help-color-invalid;
    }
  }
};
