main div[data-for="event"] {
  color: $white;
  background-image: radial-gradient(#66d1f1, #00b3e7); // TODO
  text-align: center;

  article {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      background-image: url(../images/heart-pattern.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: calc(50% + 20px) calc(-10vw - -50px);

      // transform: scale(0.8);
    }

    > header,
    > section {
      z-index: 10;
    }

    min-height: 630px;

    > section {
      p:first-child {
        font-size: rem-calc(20);
      }
      p:not(:first-child) {
        font-size: rem-calc(18);
        margin-bottom: 0;
      }
    }
  }
}
