$tooltip-width: 160px !default;
$tooltip-base-zindex: 1000 !default;

$tooltip-animation-distance: 12px;
$tooltip-gap: 3% !default;
$tooltip-top-bottom-gap-factor: 8 !default; // top and bottom needs adjustment

$tooltip-arrow-border-size: 6px;
$tooltip-alpha: 0.9;

$tip-label-input-icon-fsize: 1.25rem !default;
$tip-label-input-icon-lheight: 1.2 !default;

$tooltip-positions: (bottom, top, left, right);
@function breakpoint-tip-selector($position, $size: null) {
  $prefix: unquote('');
  @if $size != null {
    $prefix: $size + '-';
  }
  @if $position == bottom {
    @return '.has-tip, .has-tip.#{$prefix}show-bottom';
  }
  @else if $position == right-or-left {
    @return '.has-tip.#{$prefix}show-left, .has-tip.#{$prefix}show-right';
  }
  @else {
    @return '.has-tip.#{$prefix}show-#{$position}';
  }
};

@mixin tooltip-show-bottom($bg-color, $size: null) {
  $selector: breakpoint-tip-selector(bottom, $size);
  #{$selector} {
    // adjust the position for a bottom tooltip
    &:before,
    &:after {
      top: 100% - ($tooltip-gap * $tooltip-top-bottom-gap-factor);
      right: auto;
      bottom: auto;
      left: 50%;
    }

    // ajust the target arrow borders and position
    &:before {
      margin-top: -12px;
      margin-right: 0;
      margin-bottom: 0;
      margin-left: -($tooltip-arrow-border-size);

      border-color: transparent;

      border-bottom-color: $bg-color;
      border-bottom-color: hsla(hue($bg-color), saturation($bg-color), lightness($bg-color), $tooltip-alpha);
    }

    // adjust the positon for the baloon
    &:after {
      margin-left: -($tooltip-width / 2);
      box-shadow: 0 1px 1px 0 rgba(red($bg-color), green($bg-color), blue($bg-color), 0.2); // todo: verify if needed
    }

    // override the animation for bottom tooltips
    &:hover:before,
    &:hover:after,
    &:focus:before,
    &:focus:after {
      -webkit-transform: translateY(#{$tooltip-animation-distance});
      transform: translateY(#{$tooltip-animation-distance});
    }
  }
};

@mixin tooltip-show-top($bg-color, $size: null) {
  $selector: breakpoint-tip-selector(top, $size);
  // position tolltip top
  #{$selector} {
    // adjust the position for a top tooltip
    &:before,
    &:after {
      top: auto;
      right: auto;
      bottom: 100% - ($tooltip-gap * $tooltip-top-bottom-gap-factor);
      left: 50%;
    }

    // ajust the target arrow borders and position
    &:before {
      margin-top: 0; // todo?
      margin-right: 0;
      margin-bottom: -12px;
      margin-left: -($tooltip-arrow-border-size);

      border-color: transparent;

      border-top-color: $bg-color;
      border-top-color: hsla(hue($bg-color), saturation($bg-color), lightness($bg-color), $tooltip-alpha);
    }

    // adjust the positon for the baloon
    &:after {
      margin-top: 0;
      margin-right: 0;
      margin-bottom: 0;
      margin-left: -($tooltip-width / 2);
    }

    // override the animation for top tooltips
    &:hover:before,
    &:hover:after,
    &:focus:before,
    &:focus:after {
      -webkit-transform: translateY(-#{$tooltip-animation-distance});
      transform: translateY(-#{$tooltip-animation-distance});
    }
  }
};

@mixin tooltip-show-right($bg-color, $size: null) {
  $selector: breakpoint-tip-selector(right, $size);
  #{$selector} {
    // adjust the position for a right tooltip
    &:before,
    &:after {
      top: auto;
      right: auto;
      bottom: 50%;
      left: 100% - $tooltip-gap;
    }

    // ajust the target arrow borders and position
    &:before {
      margin-top: 0;
      margin-right: 0;
      margin-bottom: 0;
      margin-left: -12px;

      border-color: transparent;

      border-right-color: $bg-color;
      border-right-color: hsla(hue($bg-color), saturation($bg-color), lightness($bg-color), $tooltip-alpha);
    }

    // override the animation for right tooltips
    &:hover:before,
    &:hover:after,
    &:focus:before,
    &:focus:after {
      -webkit-transform: translateX(#{tooltip-animation-distance});
      transform: translateX(#{$tooltip-animation-distance});
    }
  }
};

@mixin tooltip-show-left($bg-color, $size: null) {
  $selector: breakpoint-tip-selector(left, $size);
  #{$selector} {
    // adjust the position for a left tooltip
    &:before,
    &:after {
      top: auto;
      right: 100% - $tooltip-gap;
      bottom: 50%;
      left: auto;
    }

    // ajust the target arrow borders and position
    &:before {
      margin-top: 0;
      margin-right: -12px;
      margin-bottom: 0;
      margin-left: 0;

      border-color: transparent;

      border-left-color: $bg-color;
      border-left-color: hsla(hue($bg-color), saturation($bg-color), lightness($bg-color), $tooltip-alpha);
    }

    // override the animation for left tooltips
    &:hover:before,
    &:hover:after,
    &:focus:before,
    &:focus:after {
      -webkit-transform: translateX(-#{$tooltip-animation-distance});
      transform: translateX(-#{$tooltip-animation-distance});
    }
  }
};

// common position adjustment for left and right tooltips
@mixin tooltip-show-right-or-left($bg-color, $size: null) {
  $selector: breakpoint-tip-selector(right-or-left, $size);
  #{$selector} {
    &:before {
      top: 3px;
    }

    &:after {
      margin-top: 0;
      margin-right: 0;
      margin-bottom: -16px;
      margin-left: 0;
    }
  }
};

@mixin tooltip-show(
  $bg-color,
  $position: bottom,
  $size: null
) {
  @if $position == bottom {
    @include tooltip-show-bottom($bg-color, $size);
  }
  @if $position == top {
    @include tooltip-show-top($bg-color, $size);
  }
  @if $position == right {
    @include tooltip-show-right($bg-color, $size);
  }
  @if $position == left {
    @include tooltip-show-left($bg-color, $size);
  }
};

@mixin icon-tooltip-show(
  $size: null
) {
  $prefix: unquote('');
  @if $size != null {
    $prefix: $size + '-';
  }

  .has-tip.#{$prefix}show-top.label-input-tip,
  .has-tip.#{$prefix}show-top.input-tip,
  .has-tip.#{$prefix}show-bottom.label-input-tip,
  .has-tip.#{$prefix}show-bottom.input-tip {
    &:before {
      margin-left: -($tooltip-arrow-border-size);
    }
    &:after {
      margin-left: -(($tooltip-width + 12px) / 2);
    }
  }

  .has-tip.#{$prefix}show-right.label-input-tip,
  .has-tip.#{$prefix}show-right.input-tip,
  .has-tip.#{$prefix}show-left.label-input-tip,
  .has-tip.#{$prefix}show-left.input-tip {
    &:before {
      margin-left: -($tooltip-arrow-border-size * 2);
    }
    &:after {
      margin-left: 0;
    }
  }
};

@mixin onedesign-tooltip(
  $bg-color: $black,
  $font-color: $white
) {
  .has-tip {
    position: relative;
    cursor: pointer;
    border-bottom: 1px dashed $anchor-color;

    &:before,
    &:after {
      position: absolute;

      // hide it
      visibility: hidden;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
      filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
      opacity: 0;

      // define the transition
      -webkit-transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, -webkit-transform 0.2s cubic-bezier(0.71, 1.7, 0.77, 1.24);
      transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, -webkit-transform 0.2s cubic-bezier(0.71, 1.7, 0.77, 1.24);
      transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, transform 0.2s cubic-bezier(0.71, 1.7, 0.77, 1.24);
      transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out, transform 0.2s cubic-bezier(0.71, 1.7, 0.77, 1.24), -webkit-transform 0.2s cubic-bezier(0.71, 1.7, 0.77, 1.24);
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);

      // todo: ?
      pointer-events: none;
    }

    // this is the arrow pointing to the tooltip target
    &:before {
      z-index: ($tooltip-base-zindex + 1);
      border: $tooltip-arrow-border-size solid transparent;
      background: transparent;
      content: "";
    }

    // tooltipe ballon
    &:after {
      z-index: $tooltip-base-zindex;
      padding: 8px;
      width: $tooltip-width;
      background-color: $bg-color; /* fallback color */
      background-color: hsla(hue($bg-color), saturation($bg-color), lightness($bg-color), $tooltip-alpha);
      color: $font-color;
      content: attr(data-tooltip);
      font-size: 14px;
      line-height: 1.2;
    }

    // hover (desktop) or focus (mobile) it should show both the arrow and ballon
    &:hover:before,
    &:hover:after,
    &:focus:before,
    &:focus:after {
      visibility: visible;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
      filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
      opacity: 1;
    }
  }

  @include tooltip-show-bottom($bg-color);
  @include tooltip-show-top($bg-color);
  @include tooltip-show-right($bg-color);
  @include tooltip-show-left($bg-color);
  @include tooltip-show-right-or-left($bg-color);

  // responsiveness
  @include -zf-each-breakpoint {
    @each $position in $tooltip-positions {
      @include tooltip-show($bg-color, $position, $-zf-size);
    }
    @include tooltip-show-right-or-left($bg-color, $-zf-size);
  }

  // special classes for icon tooltips
  .icon-tip,
  .input-tip,
  .label-input-tip {
    position: relative;
    top: 2px;
    margin-left: 6px;
    margin-right: 6px;
    border-bottom: none;
  }

  .input-tip {
    top: ($form-spacing / 2);
  }

  .label-input-tip {
    $label-h: ($form-label-font-size * $form-label-line-height);
    $input-h: ($input-font-size + ($form-spacing * 1.5) - rem-calc(1));
    $tipicon-h: ($tip-label-input-icon-fsize * $tip-label-input-icon-lheight);
    top: $label-h + (($input-h / 2) - ($tipicon-h / 2));
  }

  // responsiviness icon tooltips
  @include icon-tooltip-show;
  @include -zf-each-breakpoint {
    @include icon-tooltip-show($-zf-size);
  }
}
