div[data-for="page-footer"] {
  background-color: $light-gray;

  overflow: hidden;
  min-height: $small-footer-height;
  @include breakpoint(medium) {
    min-height: $medium-footer-height;
  }
  @include breakpoint(large) {
    min-height: $large-footer-height;
  }
}

#page-footer {
  position: relative;
  @include xy-gutters($gutter-type: padding, $gutter-position: top bottom);

  .homepage-link {
    @include xy-grid;
    @include xy-gutters($gutter-type: padding, $gutter-position: top bottom);
    justify-content: center;

    img {
      height: 45px;
      flex: 0 0 130px;  // IE: 11;
    }

    @include breakpoint(medium) {
      position: absolute;
      top: 0;
      left: map-safe-get($grid-margin-gutters, medium);
      @include xy-gutters($gutters: $half-gutters, $gutter-type: padding, $gutter-position: bottom);

      img {
        height: 40px;
        flex: 0 0 115px;  // IE: 11;
      }
    }
  }
}

#social-share-list {
  // this will handle IE 11 bug for flex
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    padding-left: 15%;
    padding-right: 15%;

    @include breakpoint(medium){
      padding-left: 30%;
      padding-right: 30%;
    }
  }

  @include xy-grid;
  justify-content: space-between;

  @include xy-gutters($gutter-type: padding, $gutter-position: bottom);
  @include xy-gutters($gutters: $double-gutters, $gutter-position: bottom);
  border-bottom: $hr-border;

  &::before,
  &::after {
    content: '';
    @include xy-cell-offset(1);
  }

  @include breakpoint(medium) {
    &::before,
    &::after {
      @include xy-cell-offset(3);
    }
  }

  @include breakpoint(medium) {
    &::before,
    &::after {
      @include xy-cell-offset(4);
    }
  }

  li {
    text-align: center;
  }

  a {
    // hide text
    text-indent: -9999px;
    background-repeat: no-repeat;
    white-space: nowrap;

    // prepare the content position
    display: inline-block;
    padding: 5px 10px;
    box-sizing: content-box;

    // prepare the background position
    background-position-x: 10px;
    background-position-y: 5px;
  }

  .facebook-link {
    width: 27px;
    height: 27px;
    background-image: url(../images/social-facebook-icon.svg);
    background-size: 24px 28px;
  }
  .instagram-link {
    width: 29px;
    height: 27px;
    background-image: url(../images/social-instagram-icon.svg);
    background-size: 24px 28px;
  }
  .twitter-link {
    width: 29px;
    height: 27px;
    background-image: url(../images/social-twitter-icon.svg);
    background-size: 35px 35px;
    background-position: 8px 0;
  }
}

#copyright-text {
  @include xy-gutters($gutter-type: padding, $gutter-position: top left right);
  @include breakpoint(medium) {
    padding-top: 0;
  }
}
